  transition: width .35s cubic-bezier(0.23, 1, 0.32, 1);
}
.sidebar-enter, .sidebar-leave-active {
  width: 0px!important;
}
.router-link-exact-active {
  background: #F5F5F7!important;
}
.sidebar {
  width: 142px;
  height: 100vh;
  background: #DDDDDD;
  flex-shrink: 0;
  overflow: hidden;
}
.sidebar-group {
  width: 142px;
}
.sidebar-item {
  padding: 0 28px;
  width: 100%;
  height: 46px;
  line-height: 46px;
  font-size: 12px;
  color: #7B7B7B;
  cursor: pointer;
  position: relative;
  &:hover {
    background: #F5F5F7;
  }
  &:before {
    content: '';
